<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <!-- 搜索表单 start -->
        <el-form
          :inline="true"
          :model="searchValidateForm"
          ref="searchValidateForm"
          label-width="100px"
          class="search-validate-form"
          :rules="searchValidateFormRules"
        >

          <!-- 请选择景区 start -->
         <el-form-item label="商店名称" prop="scenicSpotId">
         	<el-select
         		v-model="searchValidateForm.shopId"
         		placeholder="请选择商店"
         		filterable
         		clearable
         		style="width: 100%"
         	>
				<el-option
					v-for="item in shopList"
					:key="item.shopId"
					:label="item.shopName"
					:value="item.shopId"
				/>
         	</el-select>
         </el-form-item>
          <!-- 请选择景区 end -->

        </el-form>
        <!-- 搜索表单 end -->

        <!-- 查询数据 start -->
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="onSearch('searchValidateForm')"
          >搜索</el-button
        >
        <!-- 查询数据 end -->

        <!-- 重置 start -->
        <el-button type="primary" icon="el-icon-refresh-right" @click="onReset"
          >重置</el-button
        >
        <!-- 重置 end -->
		
		<el-button
		     type="primary"
		     icon="el-icon-circle-plus-outline"
		     @click="onAdd"
		     >新增</el-button
		   >
      </el-header>

      <el-main>
        <!-- 表格 start -->
        <el-table
          class="table"
          :data="tableData"
          style="width: 100%"
          border
          v-loading="tableLoading"
          :header-cell-style="{
            background: '#f2f2f2',
            color: '#606266',
            height: '38px',
            padding: '0px'
          }"
          :cell-style="{ height: '38px', padding: '0' }"
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
          >
            <template slot-scope="scope">
              <div>
                {{ tableContentValue(scope.row, value) }}
              </div>
            </template>
          </el-table-column>
          <el-table-column label="操作" min-width="120">
            <template slot-scope="scope">
              <el-button @click.native.prevent="editRow(scope.$index, scope.row)" type="primary" size="small">
                编辑
              </el-button>
			  <el-button
			    size="small"
			    type="danger"
			    @click.native.prevent="deleteRow(scope.$index, scope.row)"
			    >删除</el-button
			  >
            </template>
          </el-table-column>
        </el-table>
        <!-- 表格 end -->

        <!-- 导出的表格 start -->
        <!-- 此表格始终保持隐藏 -->
        <el-table
          id="outTable"
          class="table"
          :data="exportTableData"
          style="width: 100%; display: none"
          border
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
            align="center"
          >
          </el-table-column>
        </el-table>
        <!-- 导出的表格 end -->
      </el-main>

      <el-footer>
        <!-- 分页 start -->
        <div class="block">
          <el-pagination
            background
            layout="prev, pager, next, total, sizes, jumper"
            :total="total"
            :current-page="currentPage"
            :page-sizes="[10, 20, 30, 40]"
            :page-size="pageSize"
            @current-change="onCurrentChange"
            @size-change="onSizeChange"
          >
          </el-pagination>
        </div>
        <!-- 分页 end -->
      </el-footer>
	  <el-dialog
	    class="dialog"
		width="20%"
	    :title="drawerTitle + '商品'"
	    :visible.sync="drawer"
	    :before-close="handleClose"
	    :close-on-click-modal="false"
	  >
	      <el-form
	        ref="ruleFormRef"
	        :model="validateForm"
	        label-width="80px"
	        class="demo-ruleForm"
	        :size="formSize"
	        status-icon
			style="width:100%"
	      >
			  <el-form-item label="商店名称" prop="scenicSpotId">
				<el-select
					v-model="validateForm.shopId"
					placeholder="请选择商店"
					filterable
					clearable
					style="width: 100%"
				>
					<el-option
						v-for="item in shopList"
						:key="item.shopId"
						:label="item.shopName"
						:value="item.shopId"
					/>
				</el-select>
			  </el-form-item>
			
	      	<el-form-item label="商品名称" prop="commodityName">
	      	  <el-input v-model="validateForm.commodityName" placeholder="请输入商品名称" />
	      	</el-form-item>
			
			<el-form-item label="商品数量" prop="commodityNum">
			  <el-input v-model="validateForm.commodityNum" placeholder="请输入商品数量" />
			</el-form-item>
			
			<el-form-item label="商品价格" prop="commodityPrice">
			  <el-input v-model="validateForm.commodityPrice" placeholder="请输入商品价格" />
			</el-form-item>
			
<!-- 			<el-form-item label="商品类型" prop="commodityPrice">
			  <el-select
			  	v-model="searchValidateFormPrize.commodityType"
			  	placeholder="请选择商品类型"
			  	filterable
			  	clearable
				style="width: 100%"
			    >
			  	<el-option
			  	  v-for="item in commodityTypeList"
			  	  :key="item.value"
			  	  :label="item.label"
			  	  :value="item.value"
			  	/>
			    </el-select>
			</el-form-item> -->
			
			<el-form-item label="商品介绍" prop="commodityContent">
			  <el-input v-model="validateForm.commodityContent" placeholder="请输入商品介绍" />
			</el-form-item>
	      </el-form>
		  <div class="submit">
		    <el-button type="primary" @click="submitForm('validateForm')"
		      >提交</el-button
		    >
		    <el-button @click="cancelClick">取消</el-button>
		  </div>
	</el-dialog>
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

import { createUrl,formatDate } from '../utils/utils.js'
export default {
  name: 'SettlementFlow',
  data() {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback()
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    function checkRequireMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    }

    return {
      searchCompanyOptions: [], // 搜索的公司选项
      searchScenicOptions: [], // 搜索的景区选项
      receivingOptions: [], // 收货景区列表

      commodityTypeList: [
        {
          value: '1',
          label: '冰淇凌'
        },
        {
          value: '2',
          label: '饮料'
        },
		{
		  value: '3',
		  label: '面包'
		},
		{
		  value: '4',
		  label: '火腿肠'
		},
		{
		  value: '5',
		  label: '面巾纸'
		}
      ], // 入库类型选项

      dateStateOptions: [
        {
          value: '1',
          label: '按月查询'
        },
        {
          value: '2',
          label: '按日查询'
        },
        {
          value: '3',
          label: '按年查询'
        }
      ], // 日期统计方式选项
      searchSpotNameOptions: [], // 搜索时供货商选项
      searchGoodsNameOptions: [], // 搜索时配件名称选项

      spotIdOptions: [], // 景区选项列表

      // 快捷选项配置
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date())
            }
          },
          {
            text: '昨天',
            onClick(picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24)
              picker.$emit('pick', date)
            }
          },
          {
            text: '一周前',
            onClick(picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', date)
            }
          }
        ]
      },

      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画
      importLoading: false, // 导入excel按钮的加载动画

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数

      selectDateState: '1', // 日期统计方式（月，日，年）
      dateType: 'daterange', // 日期类型（月，日，年）
      valueFormat: 'yyyy-MM-dd', // 日期格式

      searchValidateForm: {}, // 搜索表单验证对象
      editValidateForm: {}, // 表单验证对象
      previewValidateForm: {
        previewOrderNumber: '' // 预览单号
      }, // 预览表单对象

      searchPartnerCompanyName: '', // 搜索的公司
      searchScenicName: '', // 搜索的景区
      searchSelectDateState: '1', // 搜索的日期统计方式
      searchStartEndDate: '', // 搜索的起止时间
      searchPayState: '', // 搜索的支付状态

      companys: [], // 公司列表
      companyState: '', // 当前请选择公司

      scenics: [], // 景区列表
      scenicState: '', // 当前请选择景区
	  oddNumberList:[],//单号

      downloadUrl: '',

      total: 0,

      operateStatus: 0, // 操作状态（0、初始状态；1、确认；2、撤销）

      dialogVisible: false,

      dialogState: '', // 当前dialog弹窗是新增还是编辑
      currentRowOrderNumber: '', // 当前行单号

      // previewOrderNumber: "", // 预览单号

      // 搜索表单规则
      searchValidateFormRules: {},
      // 预览表单规则
      previewValidateFormRules: {
        // 单号
        previewOrderNumber: [
          {
            validator: checkRequireMethod('单号'),
            required: true,
            trigger: ['blur', 'change']
          }
        ]
      },

      // 提交表单规则
      validateFormRules: {
        // // 机器人数量
        // robotCount: [
        //   { validator: checkNumberMethod('机器人数量'), trigger: ['blur', 'change'] }
        // ],
      },

      // 提交编辑表单规则
      editValidateFormRules: {},
	  //新增
	  showTable: true, // 是否显示表格
	    tableFields: {
			  商店名称:'shopName',
	  		  商品名称:'commodityName',
	  		  商品数量:'commodityNum',
	  		  商品价格:'commodityPrice',
	  		  商品类型:'commodityType',
			  商品介绍:'commodityContent',
			  创建时间:'createDate',
			  更新时间:'updateDate'
	      },
	      tableData: [], // 表格数据
	      tableLoading: false,
	  spotList:[],
	  shopList:[],
	  treasureTypeList:[
		  {
		    value: '1',
		    label: '代金卷',
		  },
		  {
		    value: '2',
		    label: '免单',
		  },
		  {
		    value: '3',
		    label: '实物',
		  },
		  {
		    value: '4',
		    label: '谢谢参与',
		  },
	  ],
	  prizeSizeList:[
		  {
		    value: '0',
		    label: '普通奖品',
		  },
		  {
		    value: '1',
		    label: '大奖',
		  }
	  ],
	  jackpotTypeList:[
		  {
		    value: '1',
		    label: '定点寻宝',
		  },
		  {
		    value: '2',
		    label: '定时寻宝',
		  },
	  ],
	  jackpotId:'',
	  dialogVisible:false,
	  PrizecurrentPage:1,
	  PrizePageSize:10,
	  drawer:false,
	  direction:'rtl',
	  drawerTitle:'',
	  formSize:'',
	  validateForm: {}, // 表单验证对象
	  drawerPrize:false,
	  drawerTitlePrize:'',
	  validateFormPrize:{},
	  searchValidateFormPrize:{},
	  searchValidateFormRulesPrize:{},
	  prizeData:[],
	  PrizeTotalPage:0,
	  fileList:[],
	  file:{},
	  imgBaseUrl:'https://jxzy-robot.oss-cn-beijing.aliyuncs.com/'
    }
  },
  created() {
    // 如果当前环境是开发环境才调用登录方法
    if (process.env.NODE_ENV == 'development') {
      this.login()
    }

    this.getSearchValidateForm()
	this.getSearchValidateFormPrize()
    this.getValidateForm()
	this.getSpotList()
	this.getShopList()
 //    this.getFactoryFour()
	// this.getoddNumber()
    this.initTableData()
  },
  beforeMount() {},
  mounted() {},
  methods: {
	  beforeUpload(categories,file){
		   this.file = categories
		     this.$message({
		             message: '上传成功',
		             type: 'success'
		           });
	  },
	  // 点击删除时触发
	  deleteRow(index, row) {
	    this.$confirm('此操作将删除该行数据, 是否继续?', '提示', {
	      confirmButtonText: '确定',
	      cancelButtonText: '取消',
	      type: 'warning'
	    })
	      .then(async () => {
			const params = new URLSearchParams() // 创建对象
			params.append('commodityId', row.commodityId) // 封装对象
	        const { data: res } = await this.$http.get(
	          '/Merchant/shopCommodity/deleteShopCommodity',
	          {
	            params
	          }
	        )
	        if (res.state == '200') {
	          this.$message({
	            type: 'success',
	            message: res.msg,
	            offset: 100,
	            center: true,
	            duration: 1000
	          })
	          this.initTableData()
	        } else {
	          this.$message({
	            type: 'error',
	            message: res.msg,
	            offset: 100,
	            center: true,
	            duration: 3000
	          })
	        }
	      })
	      .catch(() => {
	        this.$message({
	          type: 'info',
	          message: '已取消删除',
	          offset: 100,
	          center: true,
	          duration: 1000
	        })
	      })
	  },
	  cancelClick(){
		  this.drawer = false
		  this.validateForm = {}
	  },
	  cancelClickPrize(){
		  this.drawerPrize = false
		  this.validateFormPrize = {}
	  },
	  async getSpotList(){
		  const { data: res } = await this.$http.get('/Merchant/scenicSpot/queryScenicSpotList')
		  if(res.state == 200){
			  this.spotList = res.data
		  }
		  console.log(res)
	  },
	  async getShopList(){
	  	const { data: res } = await this.$http.post('/Merchant/sysShop/getShopList')
	  	if(res.state == 200){
	  		this.shopList = res.data.list
	  	}
	  	console.log(res)
	  },
	  // 
    // 获取搜索表单验证对象
    getSearchValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.searchValidateForm = {
        ...validateForm,
        goodId: null,
        spotId: null,
        inStockReason: ''
      }
    },
	
	getSearchValidateFormPrize() {
	  var validateForm = {}
	  for (var item in this.tableFields) {
	    validateForm[this.tableFields[item]] = ''
	  }
	  this.searchValidateFormPrize = {}
	},

    // 获取提交表单验证对象
    getValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.validateForm = validateForm
      // console.log(this.validateForm);
    },

    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post(
        '/loginSystem?username=houjingchen&password=houjingchen'
      )
    },

    /**
     * 初始化表格数据
     * @params - isPage - 是否分页
     */
    async initTableData(isPage = false) {
      this.tableLoading = true
const params = new URLSearchParams() // 创建对象
	  		params.append('shopId', this.searchValidateForm.shopId || '') // 封装对象
	      params.append('pageSize', this.currentPage) // 封装对象
	      params.append('pageNum', this.pageSize) // 封装对象
	const { data: res } = await this.$http.post(
        '/Merchant/shopCommodity/getShopCommodityList',params)
      if (res.state == '200') {
		this.tableData = res.data.list
		this.total = res.data.total
        this.tableLoading = false
      }
    },

//查看奖品
	handlePrize(index, row){
		this.jackpotId = row.jackpotId
		this.dialogVisible = !this.dialogVisible
		this.getPrizeList(this.jackpotId)
	},

	async getPrizeList(jackpotId){
		const { data: res } = await this.$http.post(
		  '/system/broadcastHunt/getTreasureNewHunt',{
			  ...this.searchValidateFormPrize,
			  jackpotId:jackpotId,
			  pageNum:this.PrizecurrentPage,
			  pageSize:this.PrizePageSize,
		})
		console.log(res)
		// if(prizeData)
		this.PrizeTotalPage = res.data.total
		this.prizeData = res.data.list
	},
    /**
     * 更改确认/撤销状态
     * @param id - 当前行数据id
     * @param status - 当前行数据status(0、初始状态；1、确认；2、撤销)
     */
    async updateStatus(id, status) {
      const { data: res } = await this.$http.post(
        '/system/inventory_detail/updateStatus',
        this.$qs.stringify({
          id: id,
          status: status // 0、初始状态；1、确认；2、撤销
        })
      )
      this.initTableData(true)
    },

    /**
     * 根据单号查询数据(预览)
     */
    async getByOrderNumber() {
      if (!this.previewValidateForm.previewOrderNumber) {
        this.previewValidateForm.previewOrderNumber = null
      }

      const { data: res } = await this.$http.get(
        '/system/inventory_detail/getByOrderNumber',
        {
          params: {
            orderNumber: this.previewValidateForm.previewOrderNumber,
            type: 1 // 1、入库；2、出库
          }
        }
      )
      return res
    },

    /**
     * 导出Excel表格的全部数据
     */
    async getExportTableData() {
      var startDate =
        (this.searchStartEndDate && this.searchStartEndDate[0]) || null
      var endDate =
        (this.searchStartEndDate && this.searchStartEndDate[1]) || null

      // console.log(startDate, endDate)

      const { data: res } = await this.$http.get(
        '/system/inventory_detail/downloadExcel',
        {
          params: {
            ...this.searchValidateForm,
            startDate,
            endDate,
            type: 1 // 1、入库；2、出库
          },
          responseType: 'arraybuffer' // 这里必须制定响应的数据类型为arraybuffer，否则导出的文件无法打开
        }
      )
      if (res) {
        const url = window.URL.createObjectURL(
          new Blob([res], {
            type:
              'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
          })
        )
        const a = document.createElement('a')
        console.log(url)
        a.href = url
        a.download = '入库信息'
        a.click()

        this.$message({
          message: '导出成功',
          type: 'success'
        })
      } else {
        this.$message({
          message: '导入失败',
          type: 'error'
        })
      }
    },

    // 导入EXCEL
    async importE(file) {
      this.importLoading = true
      var formData = new FormData()
      formData.append('file', file)

      const { data: res } = await this.$http.post(
        '/system/inventory_detail/importExcel',
        formData
      )
      this.importLoading = false
      if (res.state == '200') {
        this.$message({
          message: '导入成功！',
          type: 'success'
        })
        this.initTableData()
      } else {
        this.$message({
          message: res.msg,
          type: 'error'
        })
      }
    },

    // 点击当前行确认时触发
    confirmRow(index, row) {
      this.$confirm('确认已入库?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.updateStatus(row.id, 1)
          this.$message({
            type: 'success',
            message: '确认成功!'
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消确认操作'
          })
        })
    },

    // 点击当前行撤销时触发
    revokeRow(index, row) {
      this.$confirm('您确定要撤销吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.updateStatus(row.id, 2)
          this.$message({
            type: 'success',
