	async addTableData(){
		console.log(this.validateForm)
		const params = new URLSearchParams() // 创建对象
			params.append('shopId', this.validateForm.shopId) // 封装对象
			params.append('commodityPrice', this.validateForm.commodityPrice) // 封装对象
			params.append('commodityNum', this.validateForm.commodityNum) // 封装对象
		    params.append('commodityName', this.validateForm.commodityName) // 封装对象
		    params.append('commodityContent', this.validateForm.commodityContent) // 封装对象
		const { data: res } = await this.$http.post(
		  '/Merchant/shopCommodity/addShopCommodity',
		  params
		)
		if (res.state == '200') {
		  this.$message({
		    message: res.msg,
		    type: 'success',
		    offset: 100,
		    center: true,
		    duration: 1000
		  })
		  this.initTableData()
		} else {
		  this.$message({
		    message: res.msg,
		    type: 'error',
		    offset: 100,
		    center: true,
		    duration: 3000
		  })
		}
	},
	
	async editTableData(){
		const params = new URLSearchParams() // 创建对象
			params.append('shopId', this.validateForm.shopId) // 封装对象
			params.append('commodityPrice', this.validateForm.commodityPrice) // 封装对象
			params.append('commodityNum', this.validateForm.commodityNum) // 封装对象
		    params.append('commodityName', this.validateForm.commodityName) // 封装对象
		    params.append('commodityContent', this.validateForm.commodityContent) // 封装对象
			params.append('commodityId', this.validateForm.commodityId) // 封装对象
		const { data: res } = await this.$http.post(
		  '/Merchant/shopCommodity/updateShopCommodity',
		  params
		)
		if (res.state == '200') {
		  this.$message({
