    // 文件状态改变时的钩子，添加文件、上传成功和上传失败时都会被调用
    handleChange(file, fileList) {
      console.log(file)
      this.fileTemp = file.raw

      // 导入判断
      if (this.fileTemp) {
        if (
          this.fileTemp.type ==
            'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' ||
          this.fileTemp.type == 'application/vnd.ms-excel'
        ) {
          this.importfxx(this.fileTemp)
        } else {
          this.$message({
            type: 'warning',
            message: '附件格式错误，请删除后重新上传！'
          })
        }
      } else {
        this.$message({
          type: 'warning',
          message: '请上传附件！'
        })
      }
    },

    // 文件超出个数限制时的钩子
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 1 个文件，本次选择了 ${
          files.length
        } 个文件，共选择了 ${files.length + fileList.length} 个文件`
      )
    },

    // 文件列表移除文件时的钩子
    handleRemove(file, fileList) {
      this.fileTemp = null
    },

    // 导入格式
    importfxx(obj) {
      let _this = this
      // 通过DOM取文件数据
      this.file = obj
      var rABS = false //是否将文件读取为二进制字符串
      var f = this.file
      var reader = new FileReader()
      //if (!FileReader.prototype.readAsBinaryString) {
      FileReader.prototype.readAsBinaryString = function(f) {
        var binary = ''
        var rABS = false //是否将文件读取为二进制字符串
        var pt = this
        var wb //读取完成的数据
        var outdata
        var reader = new FileReader()
        reader.onload = function(e) {
          var bytes = new Uint8Array(reader.result)
          var length = bytes.byteLength
          for (var i = 0; i < length; i++) {
            binary += String.fromCharCode(bytes[i])
          }
          var XLSX = require('xlsx')
          if (rABS) {
            wb = XLSX.read(btoa(fixdata(binary)), {
              //手动转化
              type: 'base64'
            })
          } else {
            wb = XLSX.read(binary, {
              type: 'binary'
            })
          }
          outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]) //outdata就是你想要的东西
          console.log(outdata)
          this.da = [...outdata]
          let arr = []
          this.da.map((v) => {
            let obj = {
              robotCount: v['机器人数量'],
              type: v['发货进度'],
              applicantName: v['申请人'],
              factoryName: v['发货工厂'],
              consignorName: v['发货人'],
              receivingName: v['收货景区'],
              consigneeName: v['收货人'],
              consigneePhone: v['收货人电话'],
              receiningAddress: v['收货地址'],
              consignmentDate: v['发货时间']
            }
            arr.push(obj)
          })
          // 将 arr 赋值给 tableData，来更新表格数据
          this.tableData = arr
          return arr
        }
        reader.readAsArrayBuffer(f)
      }

      if (rABS) {
        reader.readAsArrayBuffer(f)
      } else {
        reader.readAsBinaryString(f)
      }
    },

    // 导入EXCEL文件超出限制时触发
    handleImportExceed(files) {
      this.$refs.uploadImport.clearFiles()
      // const file = files[0]
      this.$refs.uploadImport.submit(files)
    },

    // 导入EXCEL文件状态改变时触发
    handleImportChange(uploadFile, uploadFiles) {
      console.log(uploadFile)
      this.importE(uploadFile.raw)
    }
  },
  components: {
    // DownloadExcel: JsonExcel,
    // StockList
  },
  computed: {
	  setTreasuretype(){
		  return (state) =>{
			  var result = ''
			  if (state == '1') {
			    result = '代金卷'
			  } else if (state == '2') {
			    result = '免单'
			  } else if(state == '3'){
			    result = '实物'
			  }else{
			  	result = '谢谢参与'
			  }
			  return result
		  }
	  },
	  setPrizeSize(){
		  return (state) =>{
		  			  var result = ''
		  			 if (state == '0') {
		  			   result = '普通奖品'
		  			 } else if (state == '1') {
		  			   result = '大奖'
		  			 } else {
		  			   result = '暂无'
		  			 }
		  			  return result
		  }
	  },
	  setProbability(){
		  return (state) =>{
				var result = state.split("-")
				console.log(result)
				if(result[1] == 9){
					return parseFloat(state).toFixed(9) + '%'
				}else if(result[1] == 8){
					return parseFloat(state).toFixed(8) + '%'
				}else if(result[1] == 7){
					return parseFloat(state).toFixed(7) + '%'
				}else if(result[1] == 6){
					return parseFloat(state).toFixed(6) + '%'
				}else if(result[1] == 5){
					return parseFloat(state).toFixed(5) + '%'
				}else if(result[1] == 4){
					return parseFloat(state).toFixed(4) + '%'
				}else {
					var arr = parseFloat(state).toFixed(10)
					return parseFloat(arr) + '%'
				}
		  }
	  },
    tableContentValue() {
      return (row, value) => {
        // console.log(row, value);
        var result = ''
        if (value == 'type') {
          if (row[value] == 1) {
            result = '待发货'
          } else if (row[value] == 2) {
            result = '已发货'
          } else if (row[value] == 3) {
            result = '已收货'
          } else if (row[value] == 4) {
            result = '已关闭'
          }
        } else if (value == 'inStockReason') {
          if (row[value] == 1) {
            result = '生产'
          } else if (row[value] == 2) {
            result = '销售'
          }
        } else if (value == 'inStockType') {
          if (row[value] == 1) {
            result = '采购'
          } else if (row[value] == 2) {
            result = '回收'
          }
        } else if(value == 'jackpotType'){
			if(row[value] == 1){
				 result = '定点寻宝'
			}else if(row[value] == 2){
				 result = '定时寻宝'
			}
		}else {
          result = row[value] || '无'
        }
        return result
      }
    }
  }
}
